/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import Page from 'components/Layout/Page';
import { Container, Grid, Button } from '@material-ui/core';
import { Magazine as MagazineModel } from 'src/models';
import Markdown from 'components/Utils/Markdown';
import { useSpring, animated } from 'react-spring';
import { useColor } from 'hooks/useColor';

export const pageQuery = graphql`
  query($path: String!) {
    mdx(fields: { path: { eq: $path } }) {
      body
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
      }
    }
  }
`;

interface Props {
  data: {
    mdx: MagazineModel;
  };
  pageContext: {
    next: Pick<MagazineModel, 'fields' | 'frontmatter'>;
    prev: Pick<MagazineModel, 'fields' | 'frontmatter'>;
  };
}

const Magazine = ({ data, pageContext }: Props) => {
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
    config: {
      duration: 500,
    },
    delay: 400,
  });
  const colors = useColor();

  const { mdx } = data;
  if (!mdx) {
    return <div>Render Error</div>;
  }

  const { frontmatter, body } = mdx;
  const { prev, next } = pageContext;

  return (
    <Page pageName={frontmatter.title}>
      <Container
        data-type="markdown"
        css={css`
          max-width: 800px;
          margin: 3rem auto;
        `}
      >
        <Grid container>
          <Grid xs={12} sm={12} md={12} lg={12} item>
            <animated.div
              style={titleAnimation}
              css={css`
                border-bottom: 1px solid ${colors.action.hover};
                margin-bottom: 2rem;
                padding-bottom: 2rem;
              `}
            >
              <h1
                css={css`
                  font-weight: 700;
                  color: ${colors.text.primary};
                  margin-bottom: 1rem;
                `}
              >
                {frontmatter.title}
              </h1>
              <p
                css={css`
                  font-size: 1rem;
                  margin: 0;
                  font-weight: 300;
                `}
              >
                {frontmatter.date}
              </p>
            </animated.div>
            <animated.div style={descriptionAnimation}>
              <Markdown contents={body} />
            </animated.div>
          </Grid>
        </Grid>
        <Grid
          container
          css={css`
            margin-top: 5rem;
          `}
          spacing={3}
          justify="space-between"
        >
          {next && (
            <Grid item>
              <NavigationButton href={next.fields.path} variant="outlined">
                <i
                  css={css`
                    margin-right: 1rem;
                  `}
                  className="fas fa-angle-left"
                ></i>
                {next.frontmatter?.title ?? '다음 매거진'}
              </NavigationButton>
            </Grid>
          )}
          {prev && (
            <Grid item>
              <NavigationButton href={prev.fields?.path} variant="outlined">
                {prev.frontmatter?.title ?? '이전 매거진'}
                <i
                  css={css`
                    margin-left: 1rem;
                  `}
                  className="fas fa-angle-right"
                ></i>
              </NavigationButton>
            </Grid>
          )}
        </Grid>
      </Container>
    </Page>
  );
};

const NavigationButton = styled(Button)`
  width: 100%;
`;

export default Magazine;
